<template>
  <span>
      <v-container fluid class="mt-7 pt-7 mb-0 pb-0">
          <v-row>
            <v-col cols="4">
              <v-form ref="form" :value="formValid" lazy-validation>
                <v-text-field outlined v-model="jobData.JobID" :rules="jobIDRules" label="Job ID" type="number"></v-text-field>
                <v-text-field outlined v-model="jobData.UserID" :rules="userIDRules" label="User ID" type="number"></v-text-field>
                <v-spacer class="mb-5">Dimensions</v-spacer>
                <v-text-field outlined v-model="jobData.Height" :rules="boardDimensionsRules" label="Height (ft)" type="number"></v-text-field>
                <v-text-field outlined v-model="jobData.Width" :rules="boardDimensionsRules" label="Width (ft)" type="number"></v-text-field>
                <v-text-field outlined v-model="jobData.Depth" :rules="boardDimensionsRules" label="Depth (in)" type="number"></v-text-field>
              </v-form>
            </v-col>
            <v-col class="ml-10 pl-10 pr-0 mr-0" >
              <v-row align-content="center" justify="center">
                <v-col class="mr-0 pr-0">
                  <v-btn min-height="60" min-width="190" class="green darken-1" @click="StartSample"><h2>Start</h2></v-btn>
                </v-col>
                <v-col class="ml-0 pl-0">
                  <v-btn  min-height="60" min-width="190" class="red darken-1" @click="StopSample"><h2>Stop</h2></v-btn>
                </v-col>
              </v-row>
              <v-row align-content="center" justify="center">
                <v-col cols="5" class="mt-8 pt-8 ">
                  <h2 class="text-center">Sprayed:</h2>
                  <v-card outlined>
                    <h1 class="text-center">{{sprayedVolume}}</h1>
                    <h2 class="text-center">gal</h2>
                  </v-card>
                </v-col>
              </v-row>
              <v-row v-if="sampleDone" align-content="center" justify="center">
                <v-col cols="5">
                  <h2 class="text-center">Yield:</h2>
                  <v-card outlined>
                     <h1 class="text-center">{{sampleYield}}</h1>
                     <h2 class="text-center">bdft/gal</h2>
                  </v-card>
                </v-col>
              </v-row>
              <v-row v-if="sampleDone" align="center" align-content="center" justify="center">
                <v-col cols="5">
                  <v-btn @click="ClearSample" min-width="190" class="grey darken-1">Clear</v-btn>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
      </v-container>
  </span>
</template>

<script>
/* eslint-disable */

export default {
    name:'YieldCalcForm',

    data () {
        return {
            CC_TO_GAL : 3785.412,
            DIVISOR : 100.0,
            jobData: {JobID: null, UserID: null, Height: null, Width: null, Depth: null },
            formValid: false,
            jobIDRules:  [v => !!v || 'Job ID Missing'],
            userIDRules: [v=> !!v || 'User ID Missing'],
            boardDimensionsRules: [v => !!v ||'Dimension missing'],
            connected: false,
            flowData : {},
            sampleDone:false,
            materialASample: 0,
            materialBSample: 0,
            sampleBoardFeet: 0,
            sprayedVolume: 0,
            sampleYield: 0,
            sampling: false,
        }
    },
    computed:{
    },
    methods:{
        StartSample: function()
        {
          if (!this.Validate() || this.sampleDone)
          {
            return;
          }
          this.materialASample = 0;
          this.materialBSample = 0;
          this.sampleBoardFeet = this.jobData.Height * this.jobData.Width * this.jobData.Depth;
          this.sprayedVolume = 0;
          this.sampleYield = 0;
          this.sampling = true;
        },
        StopSample: function()
        {
          if (this.sprayedVolume > 0 && this.sampling)
          {
            this.sampleYield = this.sampleBoardFeet / this.sprayedVolume;
            this.sampling = false;
            this.sampleDone = true;
          } 
        },
        ClearSample: function()
        {
          this.sampleDone = false;
          this.sampleYield = 0;
          this.materialASample  = 0;
          this.materialBSample  = 0;
        },
        GetFlowData: function() 
        {
          this.sendTime = new Date().getTime();
          this.$socket.sendObj({"WGPG" : "Application_Flow"});
        },
        Validate :function()
        {
          return this.$refs.form.validate();
        },
        RecieveMessage: function(evt) 
        {
          try
          {
            if (evt.data.length > 1 && evt.data.includes("Application_Flow"))
            {
              let strData = evt.data;
              let nlIndex = strData.indexOf('\n');
              if (nlIndex > 0)
              {
                strData = strData.substring(0, nlIndex);
              }
              let dataObj = JSON.parse(strData);
              let fData = {};
              dataObj.Values.forEach(function(element){
                fData[element.MGP.MGPLabel] = element.MGP.ParamVal;
              });
              this.flowData = fData;
              if (sampling)
              {
                this.materialASample += (this.flowData.MaterialAPerCycleCC/this.DIVISOR)/this.CC_TO_GAL;
                this.materialBSample += (this.flowData.MaterialBPerCycleCC/this.DIVISOR)/this.CC_TO_GAL;
                this.sprayedVolume = this.materialASample + this.materialBSample;
              }
            }
          }
          catch(Exception)
          {
            console.log(Exception);
            console.log(evt.data.length);
            console.log(evt.data.indexOf('\n'));
            console.log(evt.data);
          }
          let interval = new Date().getTime() - this.sendTime;
          if (interval > 125)
          {
            interval = 124;
          }
          setTimeout(this.GetFlowData(), 125 - interval)
        },
        ValidateJobParams: function()
        {
         if (this.$refs.jobForm.validate())
         {
           this.jobParamsValid = true;
         }
         else
         {
            this.jobParamsValid = false;
         }
          
        },
        ValidateBoardParams: function()
        {
          if (this.$refs.boardForm.validate())
          {
             this.boardParamsValid = true;
          }
          else
          {
             this.boardParamsValid = false;
          }
         // this.boardParamsValid = (parseInt(this.jobData.Height, 10) > 0 && parseInt(this.jobData.Width, 10) > 0 && parseInt(this.jobData.Depth, 10) > 0)
        }
    },
    beforeMount()
    {
        this.$options.sockets.onopen = () =>
        {
          this.Connected = true;
          this.GetFlowData();
        },
        this.$options.sockets.onclose = () =>
        {
          this.Connected = false;
        },
        this.$options.sockets.onmessage = (evt) =>
        {
          this.RecieveMessage(evt);
        }
    },
}
</script>

