<template>
<span>
  <v-container class="mt-0 pt-1" fluid>
  <v-row class="mb-0 pb-0" align="center" justify="center" >
    <v-col class="mb-0 pb-0" cols="8" md="6" xl="3">
      <v-card class="mb-0 pb-0" dark><h2 class="green text-center font-weight-regular" >{{RecipeName}}</h2></v-card>
    </v-col> 
  </v-row>
  <v-row class="mt-0 pt-0" align="center" justify="center" >
    <v-col cols="12" xl="6" sm="8" md="8" class="text-center mt-0 pt-0">
      <v-card class="mt-0 pt-0 mb-10">
        <v-row align="center" justify="space-around" >
          <v-col class="mt-0 pt-0">
            <h2 class="font-weight-regular" >Current Index:</h2>
            <v-row align="center" justify="center">
              <v-btn @click="AdjustIndex(false)" @ class="mr-2" icon v-if="selectingIndex"><v-icon>mdi-minus</v-icon></v-btn>
              <v-btn :color="IndexBtnColor" @click="ToggleIndexSelection()">
                <h2 class="font-weight-medium black--text">{{CurrentIndexStr}}</h2>
              </v-btn>
              <v-btn @click="AdjustIndex(true)" class="ml-2" icon v-if="selectingIndex"><v-icon>mdi-plus</v-icon></v-btn>
            </v-row>
            <v-scale-transition>
            <v-row class="mt-2" v-if="selectingIndex" align="center" justify="center">
              <v-btn @click="selectingIndex = false" class="red mr-3" icon ><v-icon>mdi-close</v-icon></v-btn>
              <v-btn @click="tempSeqIndex = 0" class="grey lighten-2" icon> <v-icon>mdi-restore</v-icon></v-btn>
              <v-btn @click="SaveIndexSelection" class="green ml-3" icon ><v-icon>mdi-check</v-icon></v-btn>
            </v-row>
            </v-scale-transition>
          </v-col>
          <v-overlay :value="asData.RecipeComplete == 1">
            <v-card class="pa-5" elevation="10" light>
              <h1 class="font-weight-medium mb-4 pb-5 text-center">Recipe Complete</h1>
              <v-btn class="grey lighten-1" height="50" block @click="AcknowledgeRecipeCompletion"><h2>OK</h2></v-btn>
            </v-card>
          </v-overlay>
        </v-row>
        <v-row align="center" justify="center" >
          <v-col class="mt-0 pt-0">
            <h2 class="font-weight-regular" >Current Shot Amount:</h2>
            <h2 class="font-weight-medium">{{ShotAmount}}</h2>
          </v-col>
        </v-row>
        <v-row align="center" justify="center" >
          <v-col class="mr-6 ml-6 pr-6 pl-6 mt-0 pt-0">
            <v-sheet  tile height="38" color="black">
              <v-row>
                <v-col class="mt-0 pt-1 pl-4 pr-4">
                  <v-progress-linear  height="30" color="green" background-color="white" background-opacity="1" :value="ShotProgress"></v-progress-linear>
                </v-col>
              </v-row>
            </v-sheet>
          </v-col>
        </v-row>
        <v-row align="center" justify="center" >
          <v-scale-transition>
            <v-col class="mt-0 pt-0">
                <v-row align="center" justify="center">
                    <h2 class="font-weight-medium" >{{DispensedAmount}}</h2>
                </v-row>
              <h2 class="font-weight-regular">Dispensed</h2>
              <v-scale-transition>
              <v-btn @click="ResetDispensed" v-if="ResetDispensedEnabled" class="grey lighten-1" icon><v-icon>mdi-restore</v-icon></v-btn>
              </v-scale-transition>
            </v-col>
          </v-scale-transition>
        </v-row>
      </v-card>
      <v-row align="center" justify="space-around" >
        <v-btn class="mt-n5 pt-n2"  @click="ModeButtonPress()" 
                width="350" height="140" 
                :color="ModeButtonColor" 
                :disabled="ModeButtonDisabled">
          <h1 style="white-space:pre-wrap">{{ModeButtonText}}</h1>
        </v-btn>
      </v-row>
    </v-col> 
  </v-row>
  </v-container>
</span>
</template>

<script>

const ParamIDs = {
  SetSeqIndex:148,
  ResetDispensed:149,
  SetAutoshotEnable:150,
  ClearAlarm:152,
  AcknowledgeComplete:154
}

const Mode = {
  AUTO:0,
  MANUAL:1,
  DISPENSING:2,
  PAUSED:3,
  SCREEN_DISABLED:4,
  RATIO_ALARM:5
}

export default {
    name:'AutoShot',
    data () {
      return {
        Connected: false,
        settingMode:false,
        selectingIndex:false,
        tempSeqIndex:0,
        asData: {
          SeqIndex: 2,
          RecipeSeqLength: 15,
          shotIsVolume: 0,
          ShotAmount: 1000,
          DispensedAmount: 450,
          RecipeName: 'TestReci',
          RecipeName1: '123\u0000\u0000\u0000',
          RecipeComplete:0,
          AS_Mode:0
        },
        MsgBuffer: []
      }
    },
    computed:
    {
      RecipeName: function()
      {
        let recipeName = this.asData.RecipeName + this.asData.RecipeName1;
        let nullIndex = recipeName.indexOf("\\u0000", 0);
        if (nullIndex > 0)
        {
          recipeName = recipeName.substring(0,nullIndex);
        }
        return recipeName;
      },
      ShotAmount: function()
      {
        var str = (Math.round((this.asData.ShotAmount/1000.0) * 10) / 10).toString();
        if (this.asData.shotIsVolume == 1)
        {
          str += ' g';
        }
        else
        {
          str += ' s';
        }
        console.log(this.asData.shotIsVolume)
        return str;
      },
      DispensedAmount: function()
      {
        var str = (Math.round((this.asData.DispensedAmount/1000.0) * 10) / 10).toString();
        if (this.asData.shotIsVolume == 1)
        {
          str += ' g';
        }
        else
        {
          str += ' s';
        }
        console.log(this.asData.shotIsVolume)
        return str;
      },
      ShotProgress: function()
      {
        return (this.asData.DispensedAmount / this.asData.ShotAmount) * 100;
      },
      CurrentIndexStr: function()
      {
        let currIndex =  this.selectingIndex ? this.tempSeqIndex : this.asData.SeqIndex;
        currIndex++;
        return currIndex.toString() + '/' + this.asData.RecipeSeqLength.toString();
      },
      IndexBtnColor: function()
      {
        return this.selectingIndex ? 'blue lighten-2' : 'grey lighten-2';
      },
      ResetDispensedEnabled: function()
      {
        return this.asData.AS_Mode != Mode.DISPENSING;
      },
      ModeButtonColor: function()
      {
        if (this.asData.AS_Mode == Mode.AUTO|| this.asData.AS_Mode == Mode.DISPENSING)
        {
          return 'green';
        }
        else if (this.asData.AS_Mode == Mode.MANUAL || this.asData.AS_Mode == Mode.SCREEN_DISABLED || this.asData.AS_Mode == Mode.PAUSED)
        {
          return 'grey';
        }
        else
        {
          return 'red';
        }
      },
      ModeButtonText: function()
      {
          if (this.asData.AS_Mode == Mode.AUTO)
            return 'Autoshot\nEnabled';
          else if(this.asData.AS_Mode == Mode.MANUAL)
            return 'Manual\nMode';
          else if (this.asData.AS_Mode == Mode.DISPENSING)
            return 'Dispensing';
          else if (this.asData.AS_Mode == Mode.PAUSED)
            return 'Dispense\nPaused';
          else if (this.asData.AS_Mode == Mode.SCREEN_DISABLED)
            return 'Controller must be\n on Autoshot screen';
          else 
            return 'Clear\nRatio Alarm';
        
      },
      ModeButtonDisabled: function()
      {
        return this.asData.AS_Mode == Mode.SCREEN_DISABLED || this.asData.AS_Mode == Mode.DISPENSING || this.settingMode;
      }
    },
    methods:
    {
        ToggleIndexSelection()
        {
          if (!this.selectingIndex){
            this.selectingIndex = true;
            this.tempSeqIndex = this.asData.SeqIndex;
          }
          else
          {
            this.selectingIndex = false;
          }
        },
        AdjustIndex(increment)
        {
          if (increment && this.tempSeqIndex+1 < this.asData.RecipeSeqLength)
          {
            this.tempSeqIndex++;
          }
          else if (!increment && this.tempSeqIndex > 0)
          {
            this.tempSeqIndex--;
          }
        },
        SaveIndexSelection()
        {
          let jsonObj = {"WSP":{"WSPID":ParamIDs.SetSeqIndex,"WSPUnits":1,"WSPVal":this.tempSeqIndex.toString()}};
          this.MsgBuffer.push(jsonObj);
          this.selectingIndex = false;
        },
        ResetDispensed()
        {
          let jsonObj = {"WSP":{"WSPID":ParamIDs.ResetDispensed,"WSPUnits":1,"WSPVal":"1"}};
          this.MsgBuffer.push(jsonObj);
        },
        ModeButtonPress()
        {
          this.settingMode = true;
          if (this.asData.AS_Mode == Mode.MANUAL )
          {
            this.SetAutoMode(1);
          }
          else if (this.asData.AS_Mode == Mode.AUTO || this.asData.AS_Mode == Mode.PAUSED)
          {
            this.SetAutoMode(0);
          }
          else if(this.asData.AS_Mode == Mode.RATIO_ALARM)
          {
            this.ClearRatioAlarm();
          }
        },
        SetAutoMode(enable)
        {
          var jsonObj = {"WSP":{"WSPID":ParamIDs.SetAutoshotEnable,"WSPUnits":1,"WSPVal":enable.toString()}};
          this.MsgBuffer.push(jsonObj);
        },
        ClearRatioAlarm()
        {
          var jsonObj = {"WSP":{"WSPID":ParamIDs.ClearAlarm,"WSPUnits":1,"WSPVal":"1"}};
          this.MsgBuffer.push(jsonObj);
        },
        AcknowledgeRecipeCompletion()
        {
          var jsonObj = {"WSP":{"WSPID":ParamIDs.AcknowledgeComplete,"WSPUnits":1,"WSPVal":"1"}};
          this.MsgBuffer.push(jsonObj);
        },
        TxMsgs() 
        {
          this.sendTime = new Date().getTime();
          this.$socket.sendObj({"WGPG" : "Application_Autoshot"});
          if (this.MsgBuffer.length > 0)
          {
            console.log(this.MsgBuffer[0])
            this.$socket.sendObj(this.MsgBuffer[0]);
          }
        },
        RecieveMessage(evt)
        {         
          try
          {
            if (evt.data.length > 1 && evt.data.includes("Application_Autoshot"))
            {
              let strData = evt.data;
              let nlIndex = strData.indexOf('\n');
              if (nlIndex > 0)
              {
                strData = strData.substring(0, nlIndex);
              }
            
              let dataObj = JSON.parse(strData);
              let aData = {};
              dataObj.Values.forEach(function(element)
              {
                aData[element.MGP.MGPLabel] = element.MGP.ParamVal;
              });
              this.asData = aData;
            }
            else if (evt.data.includes("MSP"))
            {
              console.log(evt.data);
              let strData = evt.data;
              let nlIndex = strData.indexOf('\n');
              if (nlIndex > 0)
              {
                strData = strData.substring(0, nlIndex);
              }
            
              let dataObj = JSON.parse(strData);
              
              if (dataObj.MSP.status == 3)
              {
                if (dataObj.MSP.MSPID == ParamIDs.SetAutoshotEnable || dataObj.MSP.MSPID == ParamIDs.ClearAlarm)
                {
                  this.settingMode = false;
                }
                if (dataObj.MSP.MSPID == this.MsgBuffer[0].WSP.WSPID)
                {
                  console.log("Found message: " + this.MsgBuffer[0].WSP.WSPID)
                  this.MsgBuffer.shift();
                }
              }
            }
          }
          catch(Exception)
          {
            console.log(Exception);
            console.log(evt.data.length);
            console.log(evt.data.indexOf('\n'));
            console.log(evt.data);
          }
          let interval = new Date().getTime() - this.sendTime;
          if (interval > 100)
          {
            interval = 99;
          }
          setTimeout(this.TxMsgs(), 100 - interval);
        },
    },   
    beforeMount()
    {
        if (this.Connected)
        {
          this.TxMsgs();
        }
        this.$options.sockets.onopen = () =>
        {
          this.Connected = true;
          this.TxMsgs();
        },
        this.$options.sockets.onclose = () =>
        {
          this.Connected = false;
        },
        this.$options.sockets.onmessage = (evt) =>
        {
          this.RecieveMessage(evt);
        }
     }

}
</script>

<style>

</style>